body.male {
  button {
    background-color: #3CACFF !important;
  }
}

body.female {
  .top.border-bottom-wave {
    background-color: #ff676a !important;
  }

  [classname^=top-up] .top {
    background-color: #ff676a !important;
  }

  .card .top {
    background-color: #ff676a !important;
  }

  .progress-container .progress-bg-container .progress {
    background-color: #ff676a !important;
  }

  .tab-nav {
    .line {
      background-color: #ff676a !important;
    }

    .tab-item.active {
      color: #ff676a !important;
    }
  }

  .card {
    box-shadow: 0 0 30px rgba(232, 21, 21, 0.14) !important;
  }

  .level-up-container {
    color: #ff676a !important;

    .button {
      background-color: #ff676a !important;
    }
  }

  .swiper-pagination-bullet-active {
    background-color: #ff676a !important;
  }

  button {
    background-color: #ff676a !important;
  }

  .current-line {
    background-color: #ff676a !important;
  }

  .level-card-container {
    .level-card.active {
      border-color: #ff676a !important;
      color: #ff676a !important;

      .top {
        background-color: #ff676a !important;
      }
    }
  }

  .page.pay {
    background-color: #ff676a !important;

    .card-name {
      color: #ff676a !important;
    }
  }

  .top-up.selected {
    color: #ff676a !important;

    .content .top {
      background-color: #ff676a !important;
    }
  }

  .top-up-detail {
    color: #ff676a !important;
  }

  .avatar .edit {
    border-color: #ff676a !important;
  }

  .page.level .checked{
    background-color: #ff676a !important;
  }
}



