.toast {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;

  .toast-container {
    background-color: rgba(0, 0, 0, 0.8);
    border-radius: 4px;
    min-width: 160px;
    max-width: 260px;
    min-height: 92px;
    padding: 30px 25px 15px;

    .icon {
      height: 32px;
      margin-bottom: 10px;
      margin-top: 5px;
      background: transparent no-repeat center;
      background-size: contain;
    }

    .icon.success {
      background-image: url(toast-ok.png);
    }

    .icon.warning {
      background-image: url(toast-exclamation-mark.png);
    }

      @keyframes rotate {
        from {
          transform: rotate(0deg)
        }
        to {
          transform: rotate(360deg)
        }
      }
      .icon.loading {
        animation: rotate 1s linear infinite;
        transform: scaleX(-1);
        background-image: url(toast-loading.png);
      }

    p {
      margin-top: 5px;
      margin-bottom: 20px;
      min-width: 0;
      font-size: 16px;
      line-height: 1.2;
      text-align: center;
      color: #fff;
      word-wrap: break-word;
    }

    .icon + p {
      margin-bottom: 10px;
    }
  }
}



